<template>
    <div class="py-5">
        <h1 class="pb-2">Nuestras cafeterías</h1>
        <p class="pb-4">Conoce todas nuestras cafeterías y encuentra la más cercana a ti.</p>

        <div class="container-md">
            <b-card-group rows class="justify-content-center">
                <div rows v-for="cafeteria in cafeterias" :key="cafeteria.id">
                    <CardCafeteria
                        :autor="cafeteria.ciudad"
                        :srcimg="cafeteria.srcimg"
                        :alt="cafeteria.alt"
                        :ciudad="cafeteria.ciudad"
                        :titulo="cafeteria.titulo"
                        :texto="cafeteria.texto"
                        :coords="cafeteria.coords"
                    ></CardCafeteria>
                </div>
            </b-card-group>
        </div>
    </div>
</template>

<script>
import CardCafeteria from '../components/CardCafeteria.vue';

const cafeteriasJSON = '../jsons/cafeterias.json';

export default {
    title: 'Cafeterías',
    props: {
        name: {
            type: String,
            default: 'cafeterias',
        },
    },
    components: {
        CardCafeteria,
    },
    data() {
        return {
            cafeterias: [],
        };
    },
    mounted() {
        this.fetchData();
    },
    methods: {
        async fetchData() {
            const res = await fetch(cafeteriasJSON);
            const val = await res.json();
            this.cafeterias = val;
        },
    },
};
</script>

